import Vue from "vue";
import Router from "vue-router";
import HomePage from "@/views/home/Home.vue";

Vue.use(Router);

const router = new Router({
  routes: [
    { path: "/", component: HomePage },
    { path: "/dev", component: HomePage },
    { path: "**", redirect: { path: "/" } },
  ],
  mode: "history",
});

export default router;
