    <v-dialog
      v-model="dialog"
      max-width="600"
      transition="dialog-bottom-transition"
      overlay-opacity="0.9"
    >
      <v-card class="DIALOG">
        <v-toolbar color="secondary" dark dense>
          <v-toolbar-title class="text-body-2 font-weight-bold grey--text">
            Purchase Disclaimer
          </v-toolbar-title>
        </v-toolbar>
        <v-card-text class="py-4">
          <div class="TEXT">
            The Bald Head Boys would like you to know, that given we are just
            starting out, shipments may take up to ~ 4 weeks to be fulfilled.
            Also, as of now, we can only offer a refund on a case-by-case basis
            VIA a dispute email to
            <a
