import Analytics from "analytics";
import googleAnalytics from "@analytics/google-analytics";
import env from "@/assets/data/env";
import analytics from "@/assets/data/analytics";

const stats = Analytics({
  app: `bald-head-boys${env.dev ? "-dev" : ""}`,
  plugins: [
    googleAnalytics({
      trackingId: analytics.container,
    }),
  ],
});

export default {
  install(Vue) {
    Vue.prototype.$analytics = {
      events: {
        soundClick: "SoundClick",
        shirtClick: "ShirtClick",
        purchaseClick: "PurchaseClick",
      },
      track(name, payload) {
        stats.track(name, payload);
      },
      page() {
        stats.page();
      },
    };
  },
};
