<template>
  <div class="SHIRTS d-flex align-center justify-center">
    <div
      class="SHIRT primary d-flex flex-column align-center mx-8 py-4 mb-15"
      v-for="(shirt, i) in shirts"
      :key="`shirt-${i}`"
    >
      <h3 class="font-weight-bold text-uppercase pb-4">{{ shirt.name }}</h3>
      <img width="350" :src="shirt.src" :alt="shirt.name" />
      <div class="SIZE py-4">
        <v-select
          class="select"
          v-model="shirt.size"
          :items="sizes"
          menu-props="auto"
          :label="`${!shirt.error ? 'Choose Size' : 'Select a shirt size'}`"
          hide-details
          single-line
          filled
          :error="shirt.error"
        ></v-select>
      </div>
      <h4 class="font-weight-bold">CAD $30</h4>
      <v-btn
        class="mt-4"
        color="secondary"
        elevation="4"
        large
        @click="purchaseClick(shirt)"
        ><v-icon left> mdi-cart </v-icon>Purchase
      </v-btn>
    </div>
    <purchase-confirmation
      v-if="active"
      :shirt="active"
      :close="() => (active = null)"
      :proceed="proceedClick"
    />
  </div>
</template>
<script>
import PurchaseConfirmation from "./PurchaseConfirmation";
import shirts from "@/assets/data/shirts";
export default {
  name: "shirts-section",
  components: {
    PurchaseConfirmation,
  },
  computed: {
    sizes() {
      if (this.sizeMapping) return Object.keys(this.sizeMapping);
      return [];
    },
  },
  data() {
    return {
      active: null,
      shirts,
      size: {},
      sizeMapping: {
        Small: "sm",
        Medium: "md",
        Large: "lg",
        "X-Large": "xlg",
        "XX-Large": "xxlg",
      },
    };
  },
  methods: {
    proceedClick() {
      let size = this.active.sizes[this.sizeMapping[this.active.size]];
      let navigate = this.$route.path.includes("/dev");

      this.active = null;
      this.$nextTick(() => {
        if (navigate) window.open(size.dev, "_blank");
      });
    },
    purchaseClick(shirt) {
      if (!Object.keys(shirt.size).length) {
        shirt.error = true;
      } else {
        shirt.error = false;
        this.active = shirt;
        this.$analytics.track(this.$analytics.events.purchaseClick, {
          shirt: shirt.name,
        });
      }
    },
  },
};
</script>
<style lang="scss">
.SHIRTS {
  flex-wrap: wrap;
  .SHIRT {
    border-radius: 10px;
    max-width: 90%;
    img {
      max-width: 90%;
    }
    .SIZE {
      .v-select {
        .v-input__slot {
          background: var(--v-primary-darken2) !important;
        }
        &.error--text {
          .v-select__selections,
          label {
            color: var(--v-error-base) !important;
          }
        }
      }
    }
  }
}
</style>