<template>
  <div class="ICONIC-SOUNDS d-flex flex-column align-center justify-center">
    <audio
      class="mb-8"
      v-show="playing"
      :src="playing"
      autoplay
      controls
      ref="audio"
    >
      Your browser does not support the <code>audio</code> element.
    </audio>
    <div class="SOUNDS d-flex align-center justify-center">
      <div
        class="SOUND"
        v-for="item in headshots"
        :key="`headshot-${item.name}`"
        @click="play(item)"
      >
        <headshot-img :item="item" />
        <v-icon class="PLAY" size="100">mdi-play-circle</v-icon>
      </div>
    </div>
  </div>
</template>
<script>
import HeadshotImg from "@/components/Headshots/Headshots";
import headshots from "@/assets/data/headshots";
export default {
  name: "iconic-sounds",
  components: {
    HeadshotImg,
  },
  data() {
    return {
      headshots: headshots,
      playing: null,
      sounds: {
        ashley: require("@/assets/mp4/ashley.mp4"),
        dan: require("@/assets/mp4/dan.mp4"),
        bloomington: require("@/assets/mp4/bloomington.mp4"),
      },
    };
  },
  methods: {
    play(item) {
      let src = this.sounds[item.name];
      if (src !== this.playing) {
        this.playing = src;
      } else {
        this.$refs.audio.currentTime = 0;
      }
      this.$analytics.track(this.$analytics.events.soundClick, {
        name: item.name,
      });
    },
  },
};
</script>
<style lang="scss">
.ICONIC-SOUNDS {
  @keyframes font-animation {
    50% {
      font-size: 50px;
    }
  }
  .SOUNDS {
    flex-wrap: wrap;
    .SOUND {
      position: relative;
      .HEADSHOT {
        flex-basis: 300px;
        margin-bottom: 20px;
        img {
          background-color: var(--v-primary-base);
          height: 300px;
          width: 300px;
          margin: 0px 30px;
        }
      }
      .PLAY {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: rgba(0, 0, 0, 0.5);
        animation: font-animation 2.5s infinite;
        cursor: pointer;
      }
    }
  }
}
</style>